}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: "Mulish-cyrillic";
  background-color: #ffffff;
  color: #44454B;
  min-width: 320px;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}
body.no-scroll {
  overflow: hidden;
}

main {
  position: relative;
  z-index: 1;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: rgb(29, 41, 49);
  transition: 0.1s ease-in-out;
}
a:hover {
  opacity: 0.8;
}
a:active {
  opacity: 0.6;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
p {
  padding: 0;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  width: 1182px;
  margin: 0 auto;
  padding: 0 10px;
}
@media (max-width: 1300px) {
  .container {
    width: 100%;
    padding: 0 40px;
  }
}
@media (max-width: 1000px) {
  .container {
    width: 100%;
    padding: 0 20px;
  }
}
@media (max-width: 764px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}
@media (max-width: 370px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }
}

.button {
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.link {
  transition: 0.1s ease-in-out;
}
.link:hover {
  opacity: 0.8;
  transition: 0.3s;
}

label {
  transition: 0.1s ease-in-out;
  cursor: pointer;
}

input {
  transition: 0.1s ease-in-out;
  cursor: pointer;
  outline: none;
}
input:hover {
  border-color: rgba(29, 41, 49, 0.6);
}

.input-wrapper {
  position: relative;
}
.input-wrapper__label {
  display: flex;
  flex-direction: row;
}
@media (max-width: 768px) {
  .input-wrapper__label {
    justify-content: center;
    width: 328px;
  }
}
@media (max-width: 370px) {
  .input-wrapper__label {
    flex-direction: column;
    width: 100%;
  }
}
.input-wrapper label {
  width: 35%;
}
@media (max-width: 1250px) {
  .input-wrapper label {
    width: 40%;
  }
}
@media (max-width: 370px) {
  .input-wrapper label {
    width: 100%;
  }
}
.input-wrapper span {
  display: none;
  width: 55%;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  margin: 6px 0 0 10px;
}
.input-wrapper span input {
  border-color: rgb(29, 41, 49);
}
@media (max-width: 768px) {
  .input-wrapper span {
    width: 100%;
    margin: 0 0 6px 0;
    text-align: center;
  }
}
@media (max-width: 370px) {
  .input-wrapper span {
    width: 100%;
    margin: 0 0 6px 0;
    text-align: center;
  }
}
.input-wrapper.error input {
  border-color: rgb(29, 41, 49);
}
.input-wrapper.error span {
  display: block;
}

/*.active {
    color: $blue;
}*/
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes flash {
  0% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(0.8);
  }
}
@media (max-width: 370px) {
  @keyframes bounce {
    0% {
      transform: translateY(20px);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(20px);
    }
  }
}
.header {
  overflow: hidden;
  margin-bottom: 44px;
  border-bottom: 1px solid rgb(239, 239, 239);
}
.header__wrapper {
  padding: 17px 11px 21px 12px;
}
.header__contacts-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 20px;
}
.header__contacts {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 507px;
}
.header__contacts-telephone {
  position: relative;
  padding-left: 30px;
}
.header__contacts-telephone::before {
  position: absolute;
  top: -1px;
  left: 0;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/tel.svg");
  background-size: contain;
}
.header__contacts-geo {
  position: relative;
  padding-left: 19px;
}
.header__contacts-geo::before {
  position: absolute;
  top: -1px;
  left: -11px;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/geo.svg");
  background-size: contain;
}
.header__contacts-mail {
  position: relative;
  padding-left: 20px;
}
.header__contacts-mail::before {
  position: absolute;
  top: -1px;
  left: -10px;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/mail.svg");
  background-size: contain;
}
.header__dashboard {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin: 19px 0;
}
.header__search-pannel {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 100%;
}
.header__logo {
  margin-right: 11px;
}
.header__not-main-logo {
  line-height: 0;
}
.header__catalog-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 140px;
  padding: 0 0 0 33px;
  background: rgb(66, 105, 245);
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  border-radius: 8px;
  margin-right: 12px;
  transition: 0.1s ease-in-out;
}
.header__catalog-link::before {
  position: absolute;
  top: 13px;
  left: 19px;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/catalogue.svg");
  background-size: contain;
}
.header__catalog-link:hover {
  background: #254FE6;
  opacity: 1;
}
.header__search-bar-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  margin-right: 12px;
}
.header__search-bar-wrapper::after {
  position: absolute;
  top: 13px;
  right: 19px;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/lens.svg");
  background-size: contain;
  cursor: pointer;
}
.header__search-bar {
  position: relative;
  background: #F2F3F5;
  width: 100%;
  height: 100%;
  padding: 11px 20px 11px 20px;
  border: none;
  border-radius: 8px;
}
.header__search-bar::placeholder {
  color: #898D96;
}
.header__user-profile, .header__basket {
  position: relative;
  display: flex;
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  background: #F2F3F5;
  border-radius: 8px;
}
.header__user-profile {
  margin-right: 12px;
}
.header__user-profile::before {
  position: absolute;
  top: 13px;
  left: 12px;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/user.svg");
  background-size: contain;
}
.header__basket::before {
  position: absolute;
  top: 13px;
  left: 12px;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-image: url("../img/header/basket.svg");
  background-size: contain;
}
.header__navigation {
  display: flex;
  flex-flow: row nowrap;
}
.header__navigation-link a {
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #090A12;
}
.header__navigation-link:not(:last-child) {
  margin-right: 59px;
}

