  margin-bottom: 40px;
}
.contact__wrapper {
  padding-bottom: 60px;
  border-bottom: 1px solid rgb(239, 239, 239);
}
.contact__link-wrapper {
  display: flex;
  margin-bottom: 40px;
}
.contact__link {
  position: relative;
  font-size: 14px;
  line-height: 22px;
  color: #090A12;
  padding-right: 40px;
  margin-right: 13px;
}
.contact__link::before {
  position: absolute;
  top: 9px;
  right: 9px;
  content: "";
  display: block;
  width: 6px;
  height: 1px;
  background: #000;
  background-repeat: no-repeat;
  transform: rotate(45deg);
}
.contact__link::after {
  position: absolute;
  top: 13px;
  right: 9px;
  content: "";
  display: block;
  width: 6px;
  height: 1px;
  background: #000;
  background-repeat: no-repeat;
  transform: rotate(135deg);
}
.contact__active-page {
  font-size: 14px;
  line-height: 22px;
  color: #A6A6A8;
}
.contact__title {
  font-weight: 800;
  font-size: 48px;
  line-height: 60px;
  color: #090A12;
  margin-bottom: 39px;
}
.contact__info-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-bottom: 80px;
}
.contact__info-text {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  width: 560px;
}
.contact__info-list {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  margin-bottom: 40px;
}
.contact__info-item p {
  position: relative;
  font-size: 16px;
  line-height: 26px;
  padding-left: 34px;
}
.contact__info-item p::before {
  position: absolute;
  top: 1px;
  left: 0;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-size: contain;
}
.contact__info-item:not(:last-child) {
  margin-bottom: 16px;
}
.contact__info-item:nth-child(1) p::before {
  background-image: url("../img/contact/tel.svg");
}
.contact__info-item:nth-child(2) p::before {
  background-image: url("../img/contact/mail.svg");
}
.contact__info-item:nth-child(3) p::before {
  background-image: url("../img/contact/geo.svg");
}
.contact__info-description {
  font-size: 16px;
  line-height: 162%;
  padding: 24px;
  background: rgba(43, 88, 250, 0.12);
}
.contact__info-map {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 560px;
  height: 360px;
  border-radius: 4px;
}
.contact__group-wrapper {
  margin-bottom: 40px;
}
.contact__group-title {
  font-weight: 800;
  font-size: 32px;
  line-height: 44px;
  color: #090A12;
  margin-bottom: 60px;
}
.contact__group-list {
  display: flex;
  flex-flow: row wrap;
  gap: 40px;
}
.contact__group-item {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  width: 360px;
}
.contact__group-image {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-right: 25px;
}
.contact__group-info {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
}
.contact__group-name {
  font-weight: 800;
  font-size: 20px;
  line-height: 28px;
  color: #090A12;
  margin-bottom: 13px;
}
.contact__group-post {
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #5F6571;
  margin-bottom: 13px;
}
.contact__group-phone, .contact__group-mail {
  position: relative;
  font-size: 14px;
  line-height: 157%;
  padding-left: 30px;
}
.contact__group-phone::before, .contact__group-mail::before {
  position: absolute;
  top: -2px;
  left: 0;
  content: "";
  display: flex;
  width: 24px;
  height: 24px;
  background-size: contain;
}
.contact__group-phone {
  margin-bottom: 14px;
}
.contact__group-phone::before {
  background-image: url("../img/contact/tel.svg");
}
.contact__group-mail {
  margin-bottom: 24px;
}
.contact__group-mail::before {
  background-image: url("../img/contact/mail.svg");
}
.contact__group-social {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
}
.contact__group-telegram, .contact__group-whatsapp {
  display: flex;
  flex-shrink: 0;
  width: 34px;
  height: 34px;
  border-radius: 10px;
  background: #F2F3F5;
}
.contact__group-telegram {
  background-image: url("../img/contact/telegram.svg");
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 8px;
}
.contact__group-whatsapp {
  background-image: url("../img/contact/whatsapp.svg");
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: center;
}

