}
.certificates__wrapper {
  margin-left: 372px;
}
.certificates__title {
  font-weight: 800;
  font-size: 32px;
  line-height: 44px;
  color: #090A12;
  margin-bottom: 43px;
}
.certificates .slider {
  position: relative;
}
.certificates .slick-slider {
  position: relative;
  height: 100%;
  margin: 0 0 57px 0;
}
.certificates .slick-arrow {
  position: absolute;
  top: 196px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 0;
  z-index: 1;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.certificates .slick-prev.slick-arrow {
  top: -89px;
  right: 430px;
  background-image: url("../img/main/variety/arrow.svg");
  background-position: center;
  background-size: 48px 48px;
  background-repeat: no-repeat;
}
.certificates .slick-next.slick-arrow {
  top: -89px;
  right: 370px;
  background-image: url("../img/main/variety/arrow.svg");
  background-position: center;
  background-size: 48px 48px;
  background-repeat: no-repeat;
  transform: rotate(180deg);
}
.certificates .slick-dots {
  display: none;
}
.certificates .slick-list {
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 20px;
}
.certificates .slick-track {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin-bottom: 90px;
}
.certificates .slick-slide {
  overflow: hidden;
  height: auto;
}
.certificates .slick-slide:not(:first-child) {
  margin-left: 40px;
}
.certificates #cert-lightbox {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity ease 0.4s;
}
.certificates #cert-lightbox.show {
  visibility: visible;
  opacity: 1;
}
.certificates #cert-lightbox img {
  width: 560px;
  height: 792px;
  object-fit: cover;
}

