.card {
  overflow: hidden;
  width: 360px;
  border-radius: 20px;
  box-shadow: 0 4px 40px 0 rgba(0, 0, 0, 0.08);
  /*------------modal-----------------*/
}
.card__image {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 240px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #EFEFEF;
}
.card__image img {
  display: block;
  transition: 0.5s;
}
.card__image img:hover {
  transform: scale(1.7);
}
.card__link-wrapper {
  position: absolute;
  top: 24px;
  left: 25px;
  display: flex;
  flex-flow: row nowrap;
}
.card__best-sell {
  display: block;
  padding: 4px 12px;
  background: #66DBBD;
  border-radius: 8px;
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
  margin-right: 10px;
  cursor: default;
}
.card__cam-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 30px;
  background: #EFEFEF;
  border-radius: 8px;
  background-image: url("../img/category/card/cam.svg");
  background-size: 22px 22px;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 2px 2px 12px 0 rgba(0, 0, 0, 0.08);
}
.card__cam-link:hover {
  opacity: 1;
}
.card__info {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  padding: 24px;
}
.card__available-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin-bottom: 10px;
}
.card__available {
  position: relative;
  display: block;
  padding: 5px 10px 5px 27px;
  background: #EFEFEF;
  border-radius: 8px;
  font-size: 14px;
  line-height: 22px;
  color: #44454B;
  margin-right: 10px;
  cursor: default;
}
.card__available::before {
  position: absolute;
  top: 10px;
  left: 11px;
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background: #13ae5c;
  border-radius: 50%;
}
.card__quantity {
  display: block;
  padding: 5px 10px;
  background: #EFEFEF;
  border-radius: 8px;
  font-size: 14px;
  line-height: 22px;
  color: #44454B;
  cursor: default;
}
.card__text-block {
  margin-bottom: 19px;
}
.card__description {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 16px;
  line-height: 26px;
  color: #090A12;
  margin-bottom: 13px;
  height: 78px;
  overflow: hidden;
}
.card__price {
  font-size: 14px;
  line-height: 22px;
  color: #898D96;
}
.card__price span {
  font-weight: 800;
  font-size: 20px;
  line-height: 28px;
  color: #090A12;
}
.card__button-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
}
.card__basket-link {
  display: inline-flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background: #F2F3F5;
  border-radius: 8px;
  background-image: url("../img/category/card/basket.svg");
  background-size: 24px 28px;
  background-position: 13px 10px;
  background-repeat: no-repeat;
  margin-right: 17px;
}
.card__instant-buy {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 12px 5px;
  text-align: center;
  background: rgba(43, 88, 250, 0.1);
  border-radius: 8px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: rgb(66, 105, 245);
  transition: all 200ms ease;
}
.card__instant-buy:hover {
  opacity: 0.8;
}
.card__modal {
  position: relative;
}
.card .modal {
  position: fixed;
  top: 50%;
  left: 35%;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.card .modal__content {
  position: absolute;
  top: 227px;
  top: 0;
  left: 682px;
  display: flex;
  flex-flow: column nowrap;
  width: 540px;
  padding: 40px;
  border: none;
  border-radius: 12px;
  background: #ffffff;
  cursor: default;
}
.card .modal__title {
  font-weight: 800;
  font-size: 28px;
  line-height: 36px;
  margin: 0 0 24px 0;
}
.card .modal__product-info {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin-bottom: 24px;
}
.card .modal__product-image {
  overflow: hidden;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
  border-radius: 4px;
  margin-right: 24px;
}
.card .modal__product-article {
  font-size: 14px;
  line-height: 22px;
  color: #898D96;
  margin-bottom: 8px;
}
.card .modal__product-description-text {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 16px;
  line-height: 26px;
  color: #090A12;
}
.card .modal__button-wrapper {
  display: flex;
  justify-content: space-between;
}
.card .modal__close {
  position: absolute;
  top: 15px;
  right: 16px;
  display: block;
  width: 24px;
  height: 24px;
  border: none;
  background: transparent;
  cursor: pointer;
  transition: all 200ms ease;
}
.card .modal__close::before {
  position: absolute;
  top: 12px;
  left: 3px;
  content: "";
  display: block;
  width: 17px;
  height: 2px;
  background: #090A12;
  transform: rotate(45deg);
}
.card .modal__close::after {
  position: absolute;
  top: 12px;
  right: 4px;
  content: "";
  display: block;
  width: 17px;
  height: 2px;
  background: #090A12;
  transform: rotate(135deg);
}
.card .modal__close:hover {
  opacity: 0.8;
  transition: all 200ms ease;
}
.card .modal__select-field {
  margin-bottom: 16px;
}
.card .modal .select-field,
.card .modal .diameter-field {
  position: relative;
  width: 100%;
  border-radius: 8px;
  padding: 11px 20px;
  background: #F2F3F5;
  font-size: 16px;
  line-height: 26px;
  color: #090A12;
  cursor: pointer;
}
.card .modal .select-field:hover,
.card .modal .diameter-field:hover {
  box-shadow: 0px 1px 0px rgba(137, 141, 150, 0.3803921569), 0px -1px 0px rgba(137, 141, 150, 0.3803921569), 1px 0px 0px rgba(137, 141, 150, 0.3803921569), -1px 0px 0px rgba(137, 141, 150, 0.3803921569);
}
.card .modal .select-field__list,
.card .modal .diameter-field__list {
  position: absolute;
  top: 40px;
  left: 0;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  width: 100%;
  border-radius: 8px;
  padding: 11px 20px;
  background: #F2F3F5;
  font-size: 16px;
  line-height: 26px;
  color: #090A12;
}
.card .modal .select-field__item,
.card .modal .diameter-field__item {
  padding: 11px 20px 11px 1px;
}
.card .modal .diameter-field {
  margin-bottom: 16px;
}
.card .modal .counter {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 4px;
  background: #F2F3F5;
  border-radius: 8px;
  margin-bottom: 24px;
}
.card .modal .counter__button {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 4px;
  background: rgba(43, 88, 250, 0.12);
  user-select: none;
  font-weight: 800;
  font-size: 36px;
  line-height: 24px;
  text-align: center;
  padding-bottom: 7px;
  color: rgb(66, 105, 245);
}
.card .modal .counter__input {
  display: inline;
}
.card .modal .counter__input input {
  width: auto;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  color: #090A12;
  text-align: end;
  background: #F2F3F5;
  border: none;
}
.card .modal__price {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  border: 1px solid #EFEFEF;
  border-radius: 8px;
  padding: 19px;
  margin-bottom: 24px;
}
.card .modal__quantity {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  width: 194px;
}
.card .modal__quantity:nth-child(2)::before {
  position: absolute;
  top: 0;
  left: -17px;
  content: "";
  display: block;
  width: 1px;
  height: 60px;
  background: #EFEFEF;
}
.card .modal__quantity-name {
  font-size: 14px;
  line-height: 22px;
  color: #898D96;
  margin-bottom: 4px;
}
.card .modal__quantity-result {
  font-weight: 800;
  font-size: 24px;
  line-height: 142%;
  color: #090A12;
}
.card .modal__buttons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.card .modal__button-continue {
  display: block;
  width: 222px;
  padding: 12px 10px;
  border: none;
  border-radius: 8px;
  background: #F2F3F5;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #090A12;
}
.card .modal__button-add {
  display: block;
  width: 222px;
  padding: 16px 10px;
  border: none;
  border-radius: 8px;
  background: rgb(66, 105, 245);
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
}
.card .modal__name-field, .card .modal__phone-field {
  width: 100%;
  padding: 11px 20px;
  background: #f2f3f5;
  border: 0;
  border-radius: 8px;
  resize: none;
  font-size: 16px;
  line-height: 26px;
  color: rgb(137, 141, 150);
}
.card .modal__name-field:hover, .card .modal__phone-field:hover {
  box-shadow: 0px 1px 0px rgba(137, 141, 150, 0.3803921569), 0px -1px 0px rgba(137, 141, 150, 0.3803921569), 1px 0px 0px rgba(137, 141, 150, 0.3803921569), -1px 0px 0px rgba(137, 141, 150, 0.3803921569);
}
.card .modal__name-field {
  margin-bottom: 13px;
}
.card .modal__phone-field {
  margin-bottom: 23px;
}
.card .modal__privacy {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin-bottom: 27px;
}
.card .modal__checkbox {
  margin-right: 20px;
}
.card .modal__privacy-text {
  font-size: 14px;
  line-height: 22px;
}
.card .modal__link {
  font-size: 14px;
  line-height: 22px;
  text-decoration: underline;
  color: rgb(66, 105, 245);
}